/*footer*/
footer {
  background: #141414;
}

.footer__wrapper {
  width: 100%;
  height: 100%;
  padding-top: 100px;
  padding-bottom: 100px;
}

.footer__container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.footer__inc {
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  color: #FFFFFF;
  letter-spacing: 0.18px;
  line-height: 30px;
}

.footer__label span {
  font-family: 'Nunito Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  letter-spacing: 0.075px;
  color: #BCBCBC;
}

.footer__nav-list {
  display: flex;
  flex-direction: column;
}

.footer__nav-list:first-child {
  margin-right: 15px;
}

.footer__nav-list-item {
  font-family: 'Nunito Sans', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 24px;
  letter-spacing: 0.075px;
  color: #BCBCBC;
}

.footer__nav-list-item:hover {
  opacity: .7;
  transition: .2s;
}

.footer__nav-group {
  display: flex;
  flex-direction: row;
  width: 167px;
  justify-content: space-between;
}

.footer__networks {
  display: flex;
  flex-direction: column;
}

a {
  text-decoration: none;
}

@media (max-width: 786px) {
  .footer__container {
    flex-direction: column;
  }
  .footer__nav-list {
    margin-top: 40px;
  }
  .footer__container {
    flex-direction: column;
  }
  .footer__nav-list {
    margin-top: 40px;
  }
  .footer__label {
    margin-bottom: 40px;
  }
}
